@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,900&display=swap);
body{font-family:'Source Code Pro', monospace}

.sleepy-warpper{height:80px}.sleepy-warpper .logo{padding:5px;width:36px;height:28px}.sleepy-warpper .button{border:1px solid #ffffff;border-radius:0px;background-color:purple;color:#ffffff;width:110px}.sleepy-warpper .seconday{background-color:aqua;color:#ffffff}

.file-size-limit-green{color:green;margin:0px 5px}.file-size-limit-red{color:red;margin:0px 5px}

.home-container{background-color:#fff;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.home-container .file-content{flex-direction:column;justify-content:center;align-items:center;margin:10px}.home-container .content{flex-direction:column;justify-content:center;align-items:center}.home-container .content .field input[type="file"]{display:none}.home-container .content .field .custom-file-upload{border:1px solid #ccc;display:inline-block;padding:6px 12px;cursor:pointer}.home-container .content .upload-btn{background-color:#53116d;border-radius:0px;color:#fff}.modal.is-active .input{border-radius:0px;border:1px solid black}.modal.is-active .button{border:1px solid #fff;background-color:purple;color:#fff;border-radius:0px}

.file-type-green{color:green;margin:0px 5px}.file-type-red{color:red;margin:0px 5px}

.button{width:100%;background-color:#53116d;color:#ffffff;font-family:'Source Code Pro', monospace}.login-page .login-container{background-color:#EBEBEB;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.login-page .login-container .form-container{display:flex;flex-direction:column;background-color:#D9D9D9;padding:40px;width:680px;height:100hv}.login-page .login-container .form-container .field .input{border:1px solid #D9D9D9;border-radius:0px}

.button{width:100%;background-color:#53116d;color:#ffffff;font-family:'Source Code Pro', monospace}.register-page .login-container{background-color:#EBEBEB;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.register-page .login-container .form-container{display:flex;flex-direction:column;background-color:#D9D9D9;padding:40px;width:680px;height:100hv}.register-page .login-container .form-container .field .input{border:1px solid #D9D9D9;border-radius:0px}

.verify-page-container .content{background-color:#EBEBEB;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.verify-page-container .content .button-container{display:flex;flex-direction:column;background-color:#D9D9D9;padding:40px;width:680px;height:100hv}

